


































































































import { Component, Vue, Prop } from 'vue-property-decorator'
import MathJax3 from '@/components/MathJax3/index.vue'
// Hall
@Component({
  name: 'DynamicHallParams',
  components: {
    MathJax3
  }
})

export default class extends Vue {
  @Prop({ default: '' }) explainText!: string;

 public params:any= [];

 public addField() {
   this.params.push({ hi: '', bi: '', vi: '' })
 }

 public addFieldWithData(h:number, b:number, v:number) {
   this.params.push({ hi: h, bi: b, vi: v })
 }
 public removeField(index) {
   this.params.splice(index, 1)
 }

 public removeAllField() {
   let n = this.params.length
   for (let index = 0; index < n; index++) {
     this.params.splice(0, 1)
   }
 }

 public onParamsDataChange() {
   this.$emit('updateParamsData', this.params)
 }
}
